.news-page {
  overflow: hidden;
}

.news-page__top {
  padding-top: 1rem;
}

.news-page__head {
  margin-top: 5rem;
}
@media (max-width: 480px) {
  .news-page__head {
    margin-top: 2.5rem;
  }
}

.news-page__slider {
  position: relative;
  padding: 0 0 1.5rem 0;
  margin-top: 2.5rem;
}
@media (max-width: 480px) {
  .news-page__slider {
    margin-top: 1.5rem;
  }
}
.news-page__slider .news__slider-pagination {
  width: auto;
  bottom: 0;
  top: auto;
  left: 1rem;
  right: 1rem;
  z-index: 1;
  border-radius: 2.5rem;
  overflow: hidden;
  display: none;
}
@media (max-width: 480px) {
  .news-page__slider .news__slider-pagination {
    display: block;
  }
}
.news-page__slider .news__slider-pagination .swiper-pagination-progressbar-fill {
  background-color: var(--color-red);
  border-radius: 2.5rem;
}
.news-page__slider .news__card {
  background-color: var(--light-color);
  padding: 1rem;
  border-radius: 1rem;
}
.news-page__slider .news__card-link {
  padding: 0.5rem 1rem;
}

.news-page__slide {
  max-width: 28rem;
}

.news-page__filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5rem;
}
@media (max-width: 480px) {
  .news-page__filters {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 1.5rem;
  }
}

.news-page__select {
  background-color: transparent;
  border: none;
  font-family: var(--font-pt);
  font-weight: 700;
  padding: 0.5rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
}
@media (max-width: 480px) {
  .news-page__select {
    width: 100%;
    background-color: var(--light-color);
    padding: 1rem;
    border-radius: 0.5rem;
  }
}
@media (max-width: 480px) {
  .news-page__select + .news-page__select {
    margin-top: 0.5rem;
  }
}

.news-page__reset {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--color-red);
  font-family: var(--font-pt);
  font-weight: 700;
}
@media (max-width: 480px) {
  .news-page__reset {
    margin-left: auto;
    margin-top: 1rem;
  }
}

.news-page__articles {
  margin: 2.5rem 0;
}
@media (max-width: 768px) {
  .news-page__articles_desctop {
    display: none;
  }
}
.news-page__articles_mobile {
  display: none;
}
@media (max-width: 768px) {
  .news-page__articles_mobile {
    display: block;
  }
}
.news-page__articles .news__article {
  padding: 1.5rem 0 2.5rem 0;
  border-top: 1px solid rgba(180, 180, 180, 0.2);
}
.news-page__articles .news__article:last-child {
  border-bottom: 1px solid rgba(180, 180, 180, 0.2);
}
.news-page__articles .news__card {
  background-color: var(--light-color);
  padding: 1rem;
  border-radius: 1rem;
}
.news-page__articles .news__card + .news__card {
  margin-top: 0.5rem;
}
.news-page__articles .news__card-link {
  padding: 0.5rem 1rem;
}

.news-page__more {
  width: 100%;
  text-align: center;
}

.news-page__pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1rem;
}

@media (max-width: 480px) {
  .news-page__pagination-button {
    display: none;
  }
}

.news-page__pagination-button-prev,
.news-page__pagination-button-next {
  display: flex;
  align-items: center;
  color: var(--silver-color);
  transition: transform 0.2s ease, color 0.2s ease;
}
.news-page__pagination-button-prev:hover,
.news-page__pagination-button-next:hover {
  color: var(--color-red);
}

.news-page__pagination-button-prev {
  transform: rotate(90deg);
}
.news-page__pagination-button-prev:active {
  transform: scale(0.85) rotate(90deg);
}

.news-page__pagination-button-next {
  transform: rotate(-90deg);
}
.news-page__pagination-button-next:active {
  transform: scale(0.85) rotate(-90deg);
}

.news-page__pagination-list {
  display: flex;
  align-items: center;
}
@media (max-width: 480px) {
  .news-page__pagination-list {
    width: 100%;
    gap: 0.25rem;
  }
}

@media (max-width: 480px) {
  .news-page__pagination-item {
    flex: 1;
  }
}
.news-page__pagination-item_desctop {
  display: block;
}
@media (max-width: 480px) {
  .news-page__pagination-item_desctop {
    display: none;
  }
}

@media (max-width: 480px) {
  .news-page__pagination-link {
    padding: 1rem;
    width: 100%;
    text-align: center;
  }
}
.news-page__pagination-link:hover {
  color: var(--color-red);
  background-color: rgba(180, 180, 180, 0.2);
}